import { Route, Switch } from 'react-router-dom';
import React, { Component } from 'react';
import JournalGallery from './JournalGallery/JournalGallery';
import First from './WriteUps/First';
import Welcome from './Welcome';


class JournalRouting extends Component {

  render() {
    return (
	
			<Switch>
			  <Route exact path='/journal' component={Welcome} />
			  <Route exact path='/journalgallery' component={JournalGallery} />
              <Route path='/first' component={First} />
			</Switch>	
			
    );
  }
}

export default JournalRouting;