import { Route, Switch, Redirect } from 'react-router-dom';
import React, { Component } from 'react';
import Auxillary from './hoc/Auxillary/Auxillary';
import Main from './components/Main/Main';
import Journal from './components/Journal/Journal';
import Gallery from './components/Gallery/Gallery';
import Project from './components/Project/Project';
import ContactMe from './components/ContactMe/ContactMe';
import Skills from './components/Skills/Skills';
import Notfound from './Notfound';


class Routing extends Component {

  render() {
    return (
		<Auxillary>
			<Switch>
				<Route component={Main} exaxct path="/aarushibagri" />
				<Route exact path="/">
					<Redirect to="/aarushibagri" />
				</Route>
				<Route component={Gallery} exact path="/gallery" title="Gallery" />
				<Route component={Journal} exact path="/journal" title="Journal" />
				<Route component={Project} exact path="/project" title="Project" />
				<Route component={ContactMe} exact path="/contactme" title="Contact me" />
				<Route component={Skills} exact path="/skills" />
				<Route component={Notfound} />
			</Switch>	
		</Auxillary>
    );
  }
}

export default Routing;