import React, { Component } from 'react';
import Auxillary from '../../hoc/Auxillary/Auxillary';
import '../Font/Font.css';
import './Main.css';
import Aaru from '../../images/IMG_20201114_164520.jpg';
import Flask from '../../images/flask.jpg';
import Journal from '../../images/jou.jpg';
import Paint from '../../images/paint.jpg';
import Cards from './Cards/Cards';

class Main extends Component{
	componentDidMount() {
  window.scrollTo(0, 0)
  document.title = "Aarushi Bagri"
}
render() {
	return(
		
		<Auxillary>
		
     
     <div className="my-bg-photo bg-secondary text-white py-5 text-center shadow">
      <h1 className="display-4 flamenco">"Never be a liability that nobody wants, be an asset that everybody craves"</h1>
    </div>
     
     
     <div className="container-fluid" >
       <div className="row photo-bg">
          <div className="col-sm-4 " >
                <div className="photo">
                    <img src={Aaru}  alt="Aaru" />
                </div>
          </div>
          <div className="col-sm-8 name-tag pangolin" >
                <h1> Aarushi Bagri</h1>
                <h2 >
                     Programmer Analyst <br />
                    Cognizant
                </h2>
          </div>
         </div>
        </div>
     
	
		
		
        <div className="container-fluid" >   
            <div className="row">
					<Cards image={Flask} alt="Project" />
					<Cards image={Paint} alt="gallery" />
					<Cards image={Journal} alt="journal" /> 
            </div>    
        </div>
          
 
		
		</Auxillary>
    
	);
	
}
	
}

export default Main;