import React, { Component } from 'react';
import Auxillary from '../../hoc/Auxillary/Auxillary';
import '../Font/Font.css';
import './Gallery.css';
import Photos from './Photos/Photos';

import Horse from '../../images/Gallery/IMG_20200815_115343.jpg';
import Sunflower from '../../images/Gallery/IMG_20200815_131149_409.jpg';
import painting from '../../images/Gallery/IMG_20200816_182947.jpg';
import Aesthetic from '../../images/Gallery/IMG_20200818_161013_Bokeh.jpg';
import Aurora from '../../images/Gallery/IMG_20200818_165506.jpg';
import Waves from '../../images/Gallery/IMG_20200827_164708_Bokeh.jpg';
import Night from '../../images/Gallery/IMG_20200830_164947_316.jpg';
import Happy from '../../images/Gallery/IMG_20200831_170134.jpg';
import Stars from '../../images/Gallery/IMG_20200822_182348.jpg';

class Gallery extends Component{
	componentDidMount() {
  window.scrollTo(0, 0)
  document.title = "Gallery"
}

render() {
	return(
		
		<Auxillary >
     
			<div className="artbg-photo text-white py-5 text-center">
			<h1 className="display-4 flamenco" >Bienvenida a mi galeria!</h1>
			</div>
			
			<div className="container my-painting-container pt-3 cherry" >
				<div className="row zoom">
			
					<Photos image={Horse} alt="Rocking Horse" description="Somethings are evergreen" />
					<Photos image={Sunflower} alt="Sunflower" description="You're the sunflower, I think your love would be too…" />
					<Photos image={painting} alt="painting" description="Birds are great inspiration and full of colors" />
					<Photos image={Aesthetic} alt="Aesthetic" description="Skies are full of stories" />
					<Photos image={Aurora} alt="Aurora" description="Night skies" />
					<Photos image={Waves} alt="Waves" description="Waves do talk" />
					<Photos image={Night} alt="Night" description="Oh those lights that ignites fire" />
					<Photos image={Happy} alt="Happy" description="Happy Picture" />
					<Photos image={Stars} alt="Stars" description="Falling Stars" />
       

				</div>
			</div>	
		
		</Auxillary>
    
	);
	
}
	
}

export default Gallery;