import React, { Component } from 'react';
import Auxillary from '../../../hoc/Auxillary/Auxillary';
import '../../Font/Font.css';

class First extends Component{
render() {
	return( 
			<Auxillary>
				We all make choices in life some of these choices are a result of contemplating and some of haste. None the less we always end up regretting our decisions if something goes wrong but when everything is going perfectly, do we appreciate ourselves for taking the path? I don't think so, very few people would even analyze their steps. What is the main cause of being despondent? the main cause is never finding your worth, always thinking of the future rather than living in the present. None of us is ready to accept a downfall but we'll accept that we are not worth anything and why is that? because we're never taught to accept failures we're always told to best among the rest and with all these we're never able to inspect ourselves. Believe it or not, we are the ones undermining ourselves so much that no one ever could do. The world never stops and never will, if you fall you've to get up and cope up with the world but sometimes it is necessary to stop and ponder about everything you've achieved so far and what all you can do shortly. I know it's always easy to say than to implement but sometimes it just takes some initiation from your side. My grandfather always said do your best instead of worrying what will be the consequences, your hard work will always pay off no matter how difficult the situation is, there always be dawn. 
				<br/> I do always think of the times when I thought everything was falling apart but eventually they fell into the right place. Life is a jigsaw puzzle with every piece that takes its place you come closer to your destination, not just a single jigsaw but a lot of them and every puzzle represents every phase of your life. Do these jigsaws make sense from the beginning itself, the answer is no but it takes time till it all makes sense.
         
			</Auxillary>
	);
}
}

export default First;	