import React, { Component } from 'react';
import Auxillary from '../../../../hoc/Auxillary/Auxillary';

class JournalGalleryCards extends Component{
render() {
	return( 
			<Auxillary>
				<div class="col-md-4 mb-4 mb-md-0">
					<a target="_blank" href={this.props.image}>
					<img src={this.props.image} className="img-fluid" alt={this.props.alt} />
					</a>
					
				</div>
			</Auxillary>
	);
}
}

export default JournalGalleryCards;	