import React, { Component } from 'react';
import Auxillary from '../../hoc/Auxillary/Auxillary';
import './ContactMe.css';
import '../Font/Font.css';
import * as emailjs from 'emailjs-com';
import Logo from '../../images/Logo.jpg';

class ContactMe extends Component {
	componentDidMount() {
  window.scrollTo(0, 0)
  document.title = "Contact Me"
}
	
   constructor(props) {
        super(props);
    this.state= {
        name:'',
        email:'',
        message:''
    }
}

handleChangeName=(event)=>{
    this.setState({
        name:event.target.value,
    });
    
}
handleChangeEmail=(event)=>{
    this.setState({
        email:event.target.value,
    });
}

handleChangeMessage=(event)=>{
    this.setState({
        message:event.target.value,
    });
}
sendMail=(event)=>{
    event.preventDefault();

    var template_params = {
        "name": this.state.name,
        "email": this.state.email,
        "message":this.state.message,
     }
     
     var service_id = "Gmail";
     var template_id = "template_817ad5n";
     emailjs.send(service_id,template_id,template_params,'user_b5LFoFmC1biyX0UMAVxnR')
     .then(function(response) {
       alert('Message Successfully Sent!!!');
	   window.location.reload();
    }, function(err) {
       console.log('FAILED...', err);
    });
};

  render() {
    return (
      <Auxillary>
	  
				<div className="photos">
					<img src={Logo}  alt="AB" />
				</div>
	  
		<div className="col-md-6 col-md-offset-3 form-container" >
            <h2 className="pangolin" >Message</h2> 
            <p className="pangolin" > Provide your message below: </p>
          <form className="pangolin" method="POST">
	
			  <div className="row">
			  <div className="col-sm-6 form-group">
              <label>Name:</label>
              <input
                type="text"
                name="name"
                value={this.state.name}
                 className="form-control"
                onChange={this.handleChangeName}
                placeholder="Enter name"
				required
              />
			  </div>
			  </div>
			  <br />
			  
              <div className="row">
              <div className="col-sm-6 form-group">
			  <label >Email:</label>
              <input
                type="email"
                name="email"
                value={this.state.email}
                 className="form-control"
                onChange={this.handleChangeEmail}
                placeholder="Enter email"
				required
              />
			  </div>
			  </div>
			  <br />
			  
			  <div className="row">
			  <div className="col-sm-12 form-group">
              <label >Message:</label>
              <textarea
                name="message"
                className="form-control"
                value={this.state.message}
				placeHolder="Start typing..." 
				rows={5}
				maxLength="6000"
                onChange={this.handleChangeMessage}
				required
              />
			  </div>
			  </div>
			  <br /> <br />
           
			  
			  <div className="row">
				<div className="col-sm-12 form-group">
					<button variant="primary" type="submit" className="btn btn-lg btn-warning btn-block" onClick={this.sendMail}>
						Send
					</button>
				</div>
			  </div>
			
          </form>
		 
		  </div>
      </Auxillary>
    );
  }
}

export default ContactMe;