import React, { Component } from 'react';
import Auxillary from '../../hoc/Auxillary/Auxillary';
import { Route, Switch, NavLink, BrowserRouter} from 'react-router-dom';
import '../Font/Font.css';
import './Journal.css';
import JournalGallery from './JournalGallery/JournalGallery';
import First from './WriteUps/First';
import Giphy from '../../images/giphy.gif';
import Follow from '../../images/following.gif';
import Click from '../../images/click.gif';
import JournalRouting from './JournalRouting';


class Journal extends Component{
componentDidMount() {
  window.scrollTo(0, 0)
  document.title = "Journal"
}
render() {

	return(
		
		<Auxillary >
		
		<div className="bg-photo text-white py-5 text-center">
      <h1 className="display-4 flamenco">The Diligent Diary</h1>
      <p className="lead mb-0">A little more me, a little less world</p>
    </div>
	
	
	<BrowserRouter>

	<div className="subtitle text-center">
		<img src={Giphy} />
		<a href="https://www.instagram.com/the_diligent_diary" ><strong> the_diligent_diary </strong> </a>
		<img src={Follow} />
		<img src={Click}  />		
	</div>
	
     <div className="container my-custom-container pt-3" >
     <div className="row">
        <div className="col-md-5 kalam journal-tag">
		  <NavLink  activeClassName="navbar__link" to='/journalgallery' >Gallery </NavLink>
		  <br />
		  <br />
		  <h2>Thoughts</h2>
		  <NavLink activeClassName="navbar__link" to='/first' >Choices we make! </NavLink>
        </div>
		
		<div className="col-md-7 quando">
			<JournalRouting />
		</div>
             
     </div>
     </div>
		
		</BrowserRouter>	
		</Auxillary>
    
	);
	
}
	
}

export default Journal;